<template>
  <div class="balance">
    <!-- 导航条 -->
    <headCom :head="head"></headCom>
    <section class="title_on">
      <div class="content">
        <header>
          <span>当前余额</span>
          <section>
            <img
              data-v-10f76fe0
              src="data:image/png;base64,"
              height="14"
              width="14"
            >
            <span @click="click_()">余额说明</span>
          </section>
        </header>
        <p class="money_on">
          <span>0.00</span>
          <span class="yuan">元</span>
        </p>
        <div class="withdraw">提现</div>
      </div>
    </section>
    <section class="message_">
      <p>交易明细</p>
    </section>
    <section class="bottom_">
      <img
        data-v-10f76fe0
        src="data:image/png;base64,"
      >
      <p>暂无明细记录</p>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      head: {
        left:
          "<a href='javascript:history.back(-1)'><span class='iconfont icon-zuo'></span></a>",
        cont: "我的余额",
        right: ""
      }
    };
  },
  created() {},
  methods: {
    click_() {
      this.$router.push({ name: "detail" });
    }
  }
};
</script>

<style lang="scss" scoped>
.balance {
  .title_on {
    padding: 0.05rem;
    padding-top: 0.5rem;
    width: 3.65rem;
    background: #3190e8;
    .content {
      padding: 0.08rem;
      border: 0.01rem solid #3190e8;
      border-radius: 0.05rem;
      background: white;
      header {
        display: flex;
        justify-content: space-between;
        font-size: 0.1rem;
        vertical-align: middle;
        section {
          display: flex;
          align-items: center;
          line-height: 0.05rem;
          color: #3190e8;
          img {
            margin-right: 0.05rem;
          }
        }
      }
      .money_on {
        width: 100%;
        font-size: 0.35rem;
        line-height: 0.46rem;
        .yuan {
          font-size: 0.16rem;
        }
      }
      .withdraw {
        width: 100%;
        height: 0.46rem;
        text-align: center;
        line-height: 0.46rem;
        border: 0.01rem solid #ccc;
        border-radius: 0.05rem;
        background: #ccc;
        color: white;
        font-size: 0.2rem;
      }
    }
  }
  .message_ {
    padding-left: 0.15rem;
    p {
      width: 100%;
      line-height: 0.46rem;
      font-size: 0.1rem;
    }
  }
  .bottom_ {
    margin-top: 0.3rem;
    text-align: center;
    img {
      width: 1.87rem;
      height: 1.17rem;
    }
    p {
      margin-top: 0.1rem;
      font-size: 0.18rem;
    }
  }
}
</style>


